export default{
	data(){
		return{
			isShowBind:false,
			isShowAlphaView:false,
			bannerData:[],
			page:1,
			goodsList:[]
		}
	},
	onLoad() {
		
		this.getBanner();
		this.getGoods();
	},
	methods:{
		getBanner(){
			this.requestFromServer({
				url:'/index/index/homeBannerList',
				data:{
				}
			}).then((res)=>{
				console.log(res);
				this.bannerData=res.data;
						
			})
		},
		getGoods(){
			this.requestFromServer({
				url:'/index/index/productList',
				data:{
					page:this.page
				}
			}).then((res)=>{
				console.log(res);
				if(this.page==1){
					this.goodsList=[];
				}
				this.goodsList=this.goodsList.concat(res.data);
						
			})
		},
		closeMe(){
			this.isShowBind=false;
		},
		getImgUrl(icon) {
			return this.mainServer + icon;
		},
		
		seeDetail(theId){
			uni.navigateTo({
				url:'./goodsDetail?query='+theId
			})
			
		},
		
		addCart(){
			this.isShowAlphaView=true;
		   // uni.setTabBarBadge({
		   //   index: 1,
		   //   text: '2'
		   // })	
		},
		gotoLogin(){
			uni.navigateTo({
				url:'../Login/login'
			})
		},
		moveHandle(){
			//蒙版出现 禁止滑动
		},
		cancelAlpha(){
			this.isShowAlphaView=false;
		}
	}
}
